<script>
import { ref, onMounted, watch, $emit } from 'vue'
import { apiGetDisease } from "@/api/api"
import { useDiseaseStore } from '@/store/global'
import { diseaseDataHandle } from '@/utils/handle'

export default {
    emits: ["diseaseData"],
    props: {
        disease_p: Array,
    },
    setup(props, { emit }) {
        const diseaseStore = useDiseaseStore()
        let diseaseList = ref({})
        let diseaseData = ref([])
        let diseaseInfo = ref()
        const updateDiseaseData = (val) => {
            emit("diseaseData", diseaseData.value)
        }

        onMounted(async () => {
            if (diseaseStore.diseaseTypeList == undefined) {
                diseaseList.value = await apiGetDisease()
                diseaseList.value = diseaseList.value.data.data
                diseaseStore.diseaseTypeList = diseaseList.value
            } else {
                diseaseList.value = diseaseStore.diseaseTypeList
            }

            if (props.disease_p) {
                diseaseData.value = props.disease_p
                diseaseInfo.value = diseaseDataHandle(diseaseList.value, diseaseData.value)
                console.log(diseaseInfo.value)
            }
        })
        watch(() => diseaseData.value, async () => {
            diseaseInfo.value = diseaseDataHandle(diseaseList.value, diseaseData.value)
            updateDiseaseData()
        })

        const deleteDisease = (val) => {
            diseaseData.value = diseaseData.value.filter(item => item != val)
        }

        return { diseaseList, diseaseData, diseaseInfo, deleteDisease }
    },
};
</script>

<template>
    <div class="main2" v-for="(item, index) in diseaseInfo" :key="index">
        <div class="title2">{{ index }}</div>
        <span class="content2" v-for="item2 in item" :key="item2">{{ item2.name }}
            <svg width="16" height="16" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg"
                @click="deleteDisease(item2.id)">
                <path
                    d="M9.5 3.205L8.795 2.5L6 5.295L3.205 2.5L2.5 3.205L5.295 6L2.5 8.795L3.205 9.5L6 6.705L8.795 9.5L9.5 8.795L6.705 6L9.5 3.205Z"
                    fill="black" />
            </svg>
        </span>
    </div>
    <div class="mb-3">
        <label class="form-label">病症</label>
        <div class="accordion" id="accordionDisease">
            <div class="accordion-item" v-for="(item, index) in diseaseList" :key="item.id">
                <div class="accordion-header">
                    <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"
                        :data-bs-target="'#collapse' + index">{{ item.name }}</button>
                </div>
                <!-- 有三層 -->
                <div :id="'collapse' + index" class="accordion-collapse collapse" data-bs-parent="#accordionDisease"
                    v-if="item.data.length > 1">
                    <div class="accordion-body">
                        <div v-for="(item2, index2) in item.data" :key="item2.id">
                            <li class="accordion-button collapsed" data-bs-toggle="collapse"
                                :data-bs-target="'#collapseB' + index2">
                                {{ item2.name }}</li>
                            <div :id="'collapseB' + index2" class="accordion-collapse collapse"
                                :data-bs-parent="'#collapse' + index">
                                <div class="input-list">
                                    <div class="form-check-inline" v-for="item3 in item2.data" :key="item3.id">
                                        <input type="checkbox" class="form-check-input" :value="item3.id" :id="item3.id"
                                            v-model="diseaseData">
                                        <label class="form-check-label" :for="item3.id">{{ item3.name }}</label>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
                <!-- 只有兩層 -->
                <div :id="'collapse' + index" class="accordion-collapse collapse" data-bs-parent="#accordionDisease"
                    v-if="item.data.length == 1">
                    <div class="accordion-body">
                        <div class="input-list">
                            <div class="form-check-inline" v-for="item2 in item.data[0].data" :key="item2.id">
                                <input type="checkbox" class="form-check-input" :value="item2.id" :id="item2.id"
                                    v-model="diseaseData">
                                <label class="form-check-label" :for="item2.id">{{ item2.name }}</label>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


